.profile h1{padding:0 0 1rem 0}.profile h3,.profile h4{padding:.3rem 0 .3rem 0}.profile__incorrect{margin:5rem;color:#272727;text-align:center}

.cart-empty{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100vw}.cart-empty a{text-decoration:none}.cart-empty img{height:260px;padding-bottom:3rem}.cart-empty__text{max-width:200px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.cart-empty__text h3{text-align:center;color:#F25287}.cart-empty__not-worry{max-width:300px;background-color:#F25287;color:#F9F3F3;padding:1rem;margin:1rem;font-weight:900;text-transform:uppercase;border:2px solid #F25287;border-radius:.7rem;transition:0.3s}.cart-empty__not-worry:hover{background-color:#F9F3F3;border:2px solid #F25287;color:#F25287;cursor:pointer}.cart-empty__not-worry:active{background-color:#F25287;color:#F9F3F3;border:2px solid #F25287}.cart-full{height:85vh;background:#F9F3F3}

.cart-wrapper{margin:1.5rem 20rem;color:#272727}.cart-wrapper p{text-align:center;color:#464646}@media (max-width: 1300px){.cart-wrapper{margin:1.5rem 10rem}}@media (max-width: 1000px){.cart-wrapper{margin:1rem 5rem}}@media (max-width: 800px){.cart-wrapper{margin:1rem 0rem;border-radius:none}}.cart-button{color:#F9F3F3;background:#F25287;border:1px solid #F25287;width:100%;padding:.7rem 1.5rem;font-size:1rem;font-weight:700;border:unset;border-radius:6px;transition:.2s;box-shadow:-2px -1px 8px rgba(0,0,0,0.37);text-decoration:none}.cart-button:hover{background:#f02e6e}.cart-button:active{background:#F9F3F3;color:#F25287}.modal{position:fixed;top:0;left:0;z-index:101;background:rgba(10,10,10,0.075);overflow:hidden;width:100%;height:100%}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Lato', sans-serif}body{background:#ddd;overflow-x:hidden}html{font-size:15;scroll-behavior:smooth}

